.main-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
	background-color: #EEE;
}

.magicbox {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	position: relative;
	padding: 20px;
	border: 1px solid #CCC;
	border-radius: 3px;
	background-color: #FFF;
	-webkit-box-shadow: inset 0 0 3px 0 rgba(125,125,125, 0.8);
	        box-shadow: inset 0 0 3px 0 rgba(125,125,125, 0.8);
}

.magicbox__label {
	font-size: 36px;
	padding: 20px;
}

.magicbox__label--previous {
	font-size: 30px;
	color: #999;
}

.btn {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	border: none;
	border-left: 1px solid #CCC;
	border-top: 1px solid #CCC;
	border-bottom: 1px solid #CCC;
	border-top-left-radius: 3px;
	border-bottom-left-radius: 3px;
	background-color: #EEE;
	position: absolute;
  right: -1px;
  top: 48%;
	padding: 15px;
}

.input {
	font-size: 24px;
	padding: 10px;
	border: none;
	text-align: center;
}

.input + .input {
	margin-top: 15px;
}


